.home_container {/*Vai alinha tudo em coluna centralizado*/
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 4em;
}

/*Nesse prtojeto ele cria uma classe e usa as tag para ir alterando*/  
.home_container h1 {
    font-size: 2.5em;
    margin-bottom: 0.5em;
}
  
.home_container h1 span {
    color: #ffbb33;
    padding: 0 0.2em;
    background-color: #222;
}
  
.home_container p {
    margin-bottom: 1.5em;
    color: #7b7b7b;
}
  
.home_container img {
    width: 350px;
    margin: 2em 0;
}